          {
              type: 'sankey',
              top: 15,
              layout: 'none',
              nodeAlign: 'top',
              focusNodeAdjacency: 'allEdges',
              layoutIterations: 0,
              lineStyle: {
                  normal: {
                      color: 'source',
                      curveness: 0.5
                  }
              },
