         {name: 'Filler4', value: 0.00001, itemStyle: {opacity: 0,}},
         {name: 'Filler1a', value: 0.00001, itemStyle: {opacity: 0,}},
         {name: 'Humidity\n(HVAC\nCondensate)', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.HVACCondensategal,0)), itemStyle: {color:'#C7A754'}},
         {name: 'Cistern', value: cisternFlowData, itemStyle: {color:'#004D8C'}},
