         {name: 'Blowdown', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.FiltrationBlowdowngal,0)), itemStyle: {color:'#004D8C'}},
         {name: 'Sinks', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Sinksgal,0)), itemStyle: {color:'#009FB3'}},
         {name: 'Showers', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Showersgal,0)), itemStyle: {color:'#009FB3'}},
         {name: 'Misc.', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Miscellaneousgal,0)), itemStyle: {color:'#009FB3'}},
         {name: 'Fountains', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.WaterFountainsgal,0)), itemStyle: {color:'#009FB3'}},
         {name: 'Toilets', value:  toiletData, itemStyle: {color:'#009FB3'}},
         {name: 'Urinals', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Urinalsgalofpee,0)), itemStyle: {color:'#85503A'}},
         {name: 'Treatment', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.GreywaterTankIngal+arr.FiltrationBlowdowngal,0)), itemStyle: {color:'#6B983C'}},
         {name: 'Filler6', value: 0.00001, itemStyle: {opacity: 0,}},
         {name: 'Composters', value:  toiletData, itemStyle: {color:'#85503A'}},
         {name: 'Constructed\nWetlands', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.ConstructedWetlandsgal+arr.FiltrationBlowdowngal,0)), itemStyle: {color:'#6B983C'}},
         {name: 'Consumption', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Consumptiongal,0)), itemStyle: {color:'#009FB3'}},
         {name: 'Biosolids', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Biosolidsgal,0)), itemStyle: {color:'#85503A'}},
         {name: 'Leachate', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Urinalsgalofpee + this.toiletKfactor*arr.LeavingCompostersasLeachategal,0)), itemStyle: {color:'#85503A'}},
         {name: 'Overflow', value: overflowData, itemStyle: {color:'#004D8C'}},
         {name: 'Irrigation', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.HVACCondensategal+arr.CisterntoCondensategal,0)), itemStyle: {color:'#C7A754'}},
         {name: 'Filler1', value: 0.00001, itemStyle: {opacity: 0,}},
         {name: 'Filler5', value: 0.00001, itemStyle: {opacity: 0,}},
         {name: 'Filler7', value: 0.00001, itemStyle: {opacity: 0,}},
         // {name: 'fakeCisternSink', value: Math.round(this.getCisternData[1]), itemStyle: {opacity: 0,}},
      ];
      if (!this.compostersBreakdown){
        nodeData = nodeData.filter(e => e.name !== 'Biosolids');
      };
      if (!this.greywaterTreatmentBreakdown){
        nodeData = nodeData.filter(e => e.name !== 'Constructed\nWetlands' && e.name !== 'Filler5');
      };
      if (!this.waterFountainsBreakdown){
        nodeData = nodeData.filter(e => e.name !== 'Consumption');
      };
      if (!this.toiletsBreakdown){
        nodeData = nodeData.filter(e => e.name !== 'Biosolids');
      };
      if (!this.potableWaterTankBreakdown){
        nodeData = nodeData.filter(e => e.name == 'Rain' || e.name == 'Losses' || e.name == 'Cistern' || e.name == 'Humidity\n(HVAC\nCondensate)' || e.name == 'Irrigation' || e.name == 'Filtration' || e.name == 'Overflow' || e.name == 'Water\nDemand' || e.name == 'Blowdown' || e.name == 'fakeCisternSink' || e.name == 'Filler1' || e.name == 'Filler2' || e.name == 'Filler3');
      };
      if (!this.filtrationBreakdown){
        nodeData = nodeData.filter(e => e.name == 'Rain' || e.name == 'Losses' || e.name == 'Cistern' || e.name == 'Humidity\n(HVAC\nCondensate)' || e.name == 'Irrigation' || e.name == 'Filtration' || e.name == 'Overflow' || e.name == 'fakeCisternSink' || e.name == 'Filler1' || e.name == 'Filler2');
      };
      if (!this.cisternBreakdown){
        nodeData = nodeData.filter(e => e.name == 'Rain' || e.name == 'Losses' || e.name == 'Cistern' || e.name == 'Humidity\n(HVAC\nCondensate)' || e.name == 'Irrigation' || e.name == 'fakeCisternSink' || e.name == 'Filler1' || e.name == 'Filler2');
      };
      return nodeData;
    },
    sankeyLinks: function(){
      let toiletData = this.getWaterCorrections.toiletData;
      let potableWaterTankData = this.getWaterCorrections.potableWaterTankData;
      let filtrationData = this.getWaterCorrections.filtrationData;
      let overflowData = this.getWaterCorrections.overflowData;
      let cisternFlowData = this.getWaterCorrections.cisternFlowData;
      let cityWaterData = this.getWaterCorrections.cityWaterData;
      // let cisternVolumeData = this.filterData.map((currentValue)=>{currentValue.CisternVolumegal});
      return [
        {source: 'Rain', target: 'Cistern', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.Rainbeingcapturedgal,0))},
        // {source: 'Filler1', target: 'HVAC\nCondensate', value: 0.00001, lineStyle: {opacity: 0,},},
        // {source: 'Rain', target: 'Losses', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.NotCaughtRaingal,0))},
    //    {id: 'fake1', source: 'fakeCisternSource',target: 'Cistern', value: Math.round(Math.max(Math.max(...this.getCisternData[1])-this.filterData.reduce((total,arr)=>total+arr.Rainhittingroofgal,0),0)), lineStyle: {opacity: 0,}, tooltip: {show: false}},
        // {source: 'Cistern',target: 'fakeCisternSink', value: Math.round(this. getCisternData[1]), lineStyle: {opacity: 0,}, tooltip: {show: false}},
        // {source: 'Catchment', target: 'Cistern', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.Rainbeingcapturedgal,0))},
        {source: 'Humidity\n(HVAC\nCondensate)', target: 'Irrigation', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.HVACCondensategal,0))},
        // {source: 'Condensate\nTank', target: 'Filler3', value: 0.00001, lineStyle: {opacity: 0,},},
        {source: 'Cistern', target: 'Irrigation', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.CisterntoCondensategal,0))},
        {source: 'Cistern', target: 'Overflow', value: overflowData},
        // {source: 'Overflow', target: 'Filler3', value: 0.00001, lineStyle: {opacity: 0,}},
        {source: 'Cistern', target: 'Filtration', value:  filtrationData},
        {source: 'Filtration', target: 'Blowdown', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.FiltrationBlowdowngal,0))},
        {source: 'City Water', target: 'Water\nDemand', value: cityWaterData},
        {source: 'Filtration', target: 'Water\nDemand', value: potableWaterTankData},
        {source: 'Water\nDemand', target: 'Fountains', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.WaterFountainsgal,0))},
        {source: 'Water\nDemand', target: 'Toilets', value: Math.round(this.toiletKfactor*this.filterData.reduce((total,arr)=>total+arr.IntoCompostersgal + arr.Biosolidsgal*(1/this.toiletKfactor-1),0))},
        {source: 'Water\nDemand', target: 'Sinks', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Sinksgal,0))},
        {source: 'Water\nDemand', target: 'Showers', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Showersgal,0))},
        {source: 'Water\nDemand', target: 'Misc.', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Miscellaneousgal,0))},
        {source: 'Blowdown', target: 'Treatment', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.FiltrationBlowdowngal,0)),},
        {source: 'Filler4', target: 'Urinals', value: 0.00001, lineStyle: {opacity: 0,}},
        {source: 'Filler4', target: 'Blowdown', value: 0.00001, lineStyle: {opacity: 0,}},
        {source: 'Toilets', target: 'Composters', value:  toiletData},
        {source: 'Showers', target: 'Treatment', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.Showersgal,0))},
        {source: 'Sinks', target: 'Treatment', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Sinksgal,0))},
        {source: 'Misc.', target: 'Treatment', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Miscellaneousgal,0))},
        {source: 'Fountains', target: 'Consumption', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.Consumptiongal,0))},
        {source: 'Fountains', target: 'Treatment', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.WaterFountainsgal-arr.Consumptiongal,0))},
        // {source: 'Consumption', target: 'Filler5', value: 0.00001, lineStyle: {opacity: 0,}},
        {source: 'Treatment', target: 'Constructed\nWetlands', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.GreywaterTankIngal+arr.FiltrationBlowdowngal,0))},
        {source: 'Composters', target: 'Biosolids', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Biosolidsgal,0))},
        {source: 'Urinals', target: 'Leachate', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Urinalsgalofpee,0))},
        {source: 'Composters', target: 'Leachate', value:  Math.round(this.toiletKfactor*this.filterData.reduce((total,arr)=>total+arr.LeavingCompostersasLeachategal,0))},
        // {source: 'Filler', target: 'Leachate', value:  Math.round(this.filterData.reduce((total,arr)=>total+arr.Urinalsgalofpee,0))},
        {source: 'Irrigation', target: 'On-Site\nIrrigation', value: Math.round(this.filterData.reduce((total,arr)=>total+arr.RoofTopIrrigationNeedgal,0))},
        {source: 'Filler1a', target: 'Filler7', value: cisternFlowData - Math.round(this.filterData.reduce((total,arr)=>total+arr.Rainbeingcapturedgal,0)), lineStyle: {opacity: 0}},
        {source: 'Filler1', target: 'Filler2', value: 0.00001, lineStyle: {opacity: 0}},
        {source: 'Filler2', target: 'Filler3', value: cityWaterData, lineStyle: {opacity: 0}},
        {source: 'Filler3', target: 'Filler4', value: cityWaterData, lineStyle: {opacity: 0}},
        {source: 'Filler4', target: 'Filler5', value: 0.00001, lineStyle: {opacity: 0}},
        {source: 'Filler5', target: 'Filler6', value: Math.round(2*this.filterData.reduce((total,arr)=>total+arr.Consumptiongal,0)), lineStyle: {opacity: 0}},
        {source: 'Filler6', target: 'Filler7', value: 0.00001, lineStyle: {opacity: 0}},
      ];
    },
    refreshCisternVolumeChart: function(){
      this.cisternVolumeChart.showLoading();
      const cisternVolumeChartOption = {
        tooltip: {
          show: true,
          trigger: 'item',
          triggerOn: 'mousemove',
        },
        xAxis: {
          type: 'value',
          show: false,
          data: this.getCisternData[0],
        },
        yAxis: {
          type: 'value',
        },
        series: [
        {
            type: 'line',
            areaStyle: {},
            data: this.getCisternData[1],
        },
        ],
      };
      // console.log(cisternVolumeChartOption.series[0].data)
      this.cisternVolumeChart.setOption(cisternVolumeChartOption,true);
      this.cisternVolumeChart.hideLoading();
    },
    getWaterCorrections: function() {
      let waterTotals = [];
      let toiletData = [];//this.filterData.forEach((el)=>this.toiletKfactor*el.IntoCompostersgal + el.Biosolidsgal);
      let potableWaterTankData = [];//this.filterData.forEach((el)=>toiletData + el.Miscellaneousgal + el.Showersgal + el.Sinksgal + el.WaterFountainsgal);
      let filtrationData = [];
      let overflowData = [];
      let cisternVolumeData = [];
      let cisternFlowData = [];
      let cityWaterData = [];
      for(let i=0;i<this.filterData.length;i++){
        let el = this.filterData[i];
        toiletData.push(this.toiletKfactor*(el.IntoCompostersgal + (1/this.toiletKfactor-1)*el.Biosolidsgal));
        potableWaterTankData.push(toiletData[i] + el.Miscellaneousgal + el.Showersgal + el.Sinksgal + el.WaterFountainsgal);
        filtrationData.push(potableWaterTankData[i] + el.FiltrationBlowdowngal);
        if(i==0){
          //if first iteration, use the initial value from uncorrected data
          cisternVolumeData.push(el.CisternVolumegal);
        }else{
          //else need to use previous cistern volume corrected number
          cisternVolumeData.push(cisternVolumeData[i-1]);
        };
        overflowData.push(el.Rainbeingcapturedgal - filtrationData[i] - el.CisterntoCondensategal);
        if(overflowData[i] < 0){
          // more demand than rain, so need to take water from cistern
          cisternVolumeData[i] = (cisternVolumeData[i] - (-1*overflowData[i]));
          overflowData[i] = 0;
          if(cisternVolumeData[i] < 0){
            //but if cistern didn't have enough water, need to take from city water
            cityWaterData.push(-1*cisternVolumeData[i]);
            cisternVolumeData[i] = 0;
          }else{
            cityWaterData.push(0);
          };
        }else{
          // let overflow go into cistern if room
          cisternVolumeData[i] = cisternVolumeData[i] + overflowData[i];
          if(cisternVolumeData[i] > 50000){
            overflowData[i] = (cisternVolumeData[i] - 50000);
            cisternVolumeData[i] = 50000;
          }else{
            overflowData[i] = 0;
          };
        };
      };
      waterTotals.toiletData = Math.round(toiletData.reduce((total,arr)=>total+arr,0));
      waterTotals.potableWaterTankData = Math.round(potableWaterTankData.reduce((total,arr)=>total+arr,0));
      waterTotals.filtrationData = Math.round(filtrationData.reduce((total,arr)=>total+arr,0));
      waterTotals.overflowData = Math.round(overflowData.reduce((total,arr)=>total+arr,0));
      waterTotals.cisternFlowData = Math.round(waterTotals.overflowData + waterTotals.filtrationData + this.filterData.reduce((total,arr)=>total+arr.CisterntoCondensategal,0));
      waterTotals.cityWaterData = Math.round(cityWaterData.reduce((total,arr)=>total+arr,0));
      // let fakeCisternSink = cisternData - demandData;
      return waterTotals;
    },
  },
  mounted: function () {
    echarts.registerTheme('shine', shine)
    this.waterSankey = echarts.init(this.$refs.waterSankey,'shine');
    this.refreshWaterSankey;
    this.waterSankey.on('click',{seriesIndex: 0},this.toggleData);
    // this.waterSankey.on('mouseover',{seriesIndex: 0, dataType: 'edge'}, (el)=>this.waterSankey.dispatchAction({type: 'showTip', seriesIndex: 0, name: el.name}));
    // this.cisternVolumeChart = echarts.init(this.$refs.cisternVolumeChart,'shine');
    // this.refreshCisternVolumeChart;
    this.onResize();
  }
}
</script>
